footer {
	padding: 30px 8% 130px 8%;
	display: flex;
	flex-direction: column-reverse;
	background-color: #000;
	position: relative;
	div {
		ul {
			display: flex;
			li {
				position: relative;
				&:first-child {
					margin-right: 10px;
					padding-right: 10px;
					&::after {
						content: "";
						position: absolute;
						top: 6px;
						right: 0;
						width: 2px;
						height: 14px;
						background-color: #ffffff; } }
				a {
					display: block;
					color: #ffffff;
					font-size: 14px;
					letter-spacing: -0.28px;
					line-height: 20px;
					font-weight: bold; } } }
		div {
			margin-top: 5px;
			address {
				@include font12();
				color: #c9c9c9; } }
		p {
			margin-top: 50px;
			@include font12(); } }
	.fooLogo {
		margin-bottom: 10px;
		position: absolute;
		bottom: 155px;
		right: 0;
		transform: rotate(90deg); } }
