
/* 컴파일되는
	* 여러 줄
	* 주석 */
// variable
$frontColor: #00cdff;
$backColor: #7d14ff;
$black: #000000;
$white: #ffffff;
$bgColor: #0d1c2f;
$bgColor02: #6414ff;
$bgColor03: #171718;
$bgColor04: #0e1c30;
$bgColor05: #0091ff;
$lightGray: #575757;
$wrongColor: #ff3eb5;
$point: #87fa82;
$darkGray: #c9c9c9;


//mixin
@mixin NotoSans () {
	font-family: 'Noto Sans KR', sans-serif; }

@mixin colorGradation() {
	background: linear-gradient(to right, $frontColor, $backColor);
	color: transparent;
	-webkit-background-clip: text;
	font-family: 'Noto Sans KR black', sans-serif; }

@mixin borderGradient() {
    border: 1px solid transparent;
    background-image: linear-gradient($bgColor, $bgColor), linear-gradient(100deg, $frontColor, $backColor);
    background-origin: border-box;
    background-clip: content-box, border-box; }

@mixin font12() {
	font-size: 12px;
	letter-spacing: -0.24px;
	line-height: 1.4;
	font-family: 'Noto Sans KR bold'; }
@mixin font14() {
	font-size: 14px;
	letter-spacing: -0.28px;
	line-height: 1.4;
	font-family: 'Noto Sans KR bold'; }
@mixin font16Gradation() {
	@include colorGradation();
	font-size: 16px;
	line-height: 25px;
	letter-spacing: -0.32px; }
@mixin font16() {
	font-size: 16px;
	line-height: 25px;
	letter-spacing: -0.32px;
	font-family: 'Noto Sans KR bold'; }
@mixin font20() {
	font-size: 20px;
	line-height: 1.6;
	letter-spacing: -0.4px;
	font-family: 'Noto Sans KR bold'; }
@mixin font24Gradation() {
	@include colorGradation();
	font-size: 24px;
	line-height: 1.6;
	letter-spacing: -0.48px; }
@mixin font24() {
	font-size: 24px;
	line-height: 1.6;
	letter-spacing: -0.48px;
	font-family: 'Noto Sans KR bold'; }
@mixin font44() {
	@include colorGradation();
	font-size: 44px;
	line-height: 1.1;
	letter-spacing: -0.88px; }
@mixin moreArrow() {
	width: 10px;
	height: 10px;
	border-top: 1px solid $white;
	border-right: 1px solid $white;
	transform: rotate(45deg); }

